import React from 'react'
import contactUsBG from "../assets/contact_us_bg.jpg";
import {Col, Container, Row} from "reactstrap";
import {SECTION_STYLES} from "./Layout";


function ContactUs() {
  return (
    <div id='contact-us'
         style={{backgroundImage: `url(${contactUsBG})`, backgroundPosition: 'top', backgroundSize: 'cover'}}>
      <div style={{backgroundColor: 'rgba(255, 255, 255, 0.4)'}}>
        <Container>
          <Row>
            <Col style={{display: 'table'}}>
              <section style={{...SECTION_STYLES, height: '1000px'}}>
                <h2><a href='mailto:info@wholehearted.kr' style={{color: 'rgb(19, 27, 77)'}}>info@wholehearted.kr</a>
                </h2>
              </section>
            </Col>
          </Row>
        </Container>
      </div>
    </div>
  );
}

export default ContactUs;
