import React from 'react'
import productsBG from "../assets/products_bg.jpg";
import {Col, Container, Row} from "reactstrap";
import itTalents from "../assets/it_talents.jpg";
import farmingEquipment from "../assets/farming_equipment.jpg";
import covid19Products from "../assets/covid19_products.jpg";
import secondHandedHeavyMachinery from "../assets/2nd_handed_heavy_machinery.jpg";
import {SECTION_STYLES} from "./Layout";


function Products() {
  return (
    <div id='products'
         style={{backgroundImage: `url(${productsBG})`, backgroundPosition: 'center', backgroundSize: 'cover'}}>
      <div style={{backgroundColor: 'rgba(255, 255, 255, 0.4)'}}>
        <Container>
          <Row style={{padding: '3rem 0'}}>
            <Col style={{display: 'table', backgroundColor: 'rgba(255, 255, 255, 0.8)'}}>
              <section style={{...SECTION_STYLES}}>
                <Row>
                  <Col>
                    <div style={{
                      width: '150px',
                      height: '150px',
                      background: `url(${itTalents})`,
                      backgroundSize: 'cover',
                      borderRadius: '75px',
                      margin: 'auto',
                    }}/>
                    <br/>
                    <h5>IT Talents</h5>
                  </Col>

                  <Col>
                    <div style={{
                      width: '150px',
                      height: '150px',
                      background: `url(${farmingEquipment})`,
                      backgroundSize: 'cover',
                      borderRadius: '75px',
                      margin: 'auto',
                    }}/>
                    <br/>
                    <h5>Farming Equipment</h5>
                  </Col>

                  <Col>
                    <div style={{
                      width: '150px',
                      height: '150px',
                      background: `url(${covid19Products})`,
                      backgroundSize: 'cover',
                      borderRadius: '75px',
                      margin: 'auto',
                    }}/>
                    <br/>
                    <h5>COVID-19 Products</h5>
                  </Col>

                  <Col>
                    <div style={{
                      width: '150px',
                      height: '150px',
                      background: `url(${secondHandedHeavyMachinery})`,
                      backgroundSize: 'cover',
                      borderRadius: '75px',
                      margin: 'auto',
                    }}/>
                    <br/>
                    <h5>2<sup>nd</sup> handed<br/>heavy machinery</h5>
                  </Col>
                </Row>
              </section>
            </Col>
          </Row>
        </Container>
      </div>
    </div>
  );
}

export default Products;
